        </v-dialog>



        <DeleteModal :deleteModal="deleteModal"></DeleteModal>
    </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'
import { priceMixin } from '@/mixins/formatter.js';
import { loaderMixin } from '@/mixins/dateLoaderMixins.js'
import DeleteModal from '@/components/modals/DeleteModal.vue'

export default {
    mixins: [priceMixin, loaderMixin],
    components: {
        DeleteModal
    },
    data() {
        return {
            editModal: false,
            deleteModal: false,
            headers: [
                {text: 'Status', value: 'status', align: 'left'},
                {text: 'Qayerga', value: 'customer', align: 'center'},
                {text: 'Nomi', value: 'name', align: 'center'},
                {text: 'Dona', value: 'amount', align: 'center'},
                {text: 'Dona narxi', value: 'price', align: 'center'},
                {text: 'Summasi', value: 'total_price', align: 'center'},
                {text: 'Sana', value: 'date', align: 'center'},
                {text: 'Harakatlar', value: 'actions', align: 'center'},
            ],
            items: []
        }
    },
    async mounted(){
        await this.refresh(this.page)
    },
    watch:{
        page: async function (newVal, oldVal) {
            if (newVal) {
                await this.refresh(newVal)
            }
        },
        '$store.state.main.ready_products_outcome': {
            handler: async function(value) {
                await this.refresh()
                this.$store.commit('main/SET_UPDATE_READY_PRODUCTS_OUTCOME', false)
            },
            deep: true,
            immediate: true
        },
    },
    computed: {
        ...mapState({
            sales: state => state.readyProducts.sales
        })
    },
    methods: {
        ...mapActions({
            fetchSales: 'readyProducts/fetchSales',
        }),
        async refresh(page){
            this.loading = true;
            await this.fetchSales({page: page, category_id: this.$route.params.id});
            this.page = this.sales.current_page;
            this.pageCount = Math.ceil(this.sales.total / 10);
            this.items = this.sales.data;
            this.items.forEach((elem) => {
                elem.name = elem.product.name;    
                elem.code = elem.product.code;
                elem.date = this.$dayjs(elem.created_at).format('DD-MM-YYYY');
            })
            this.loading = false;
        },
        closeEditModal(){
            this.editModal = false
        },
    }
};
</script>

